.btn-action-group {
  display: inline-block;
  position: fixed;
  bottom: 0;
  right: 25px;
  margin: 0;
  padding: 1rem;
  list-style: none;
  text-align: right;
}
.btn-action-group li {
  margin-bottom: 2rem;
}
.btn-action-group li:not(.primary) {
  position: fixed;
  bottom: -3000px;
}
.btn-action-group li .label {
  position: relative;
  right: -10px;
  margin-right: 1.5rem;
  padding: 0.5rem;
  font-size: 1.1rem;
  font-weight: 400;
  letter-spacing: 0.5px;
  color: #ffffff;
  background-color: #616161;
  opacity: 0;
}
.btn-action-group li.primary {
  bottom: 0;
  opacity: 1;
  transform: scale(1);
}
.btn-action-group li.primary .btn {
  opacity: 1;
}
.btn-action-group li.primary .btn i {
  opacity: 1;
}
.btn-action-group li.primary .btn i.hover {
  display: none;
}
.btn-action-group:hover li .btn i.default {
  display: none;
}
.btn-action-group:hover li .btn i.hover {
  display: block;
}
.btn-action-group:hover li .label {
  -webkit-animation: label-animation 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  right: 0;
  opacity: 1;
}
.btn-action-group:hover li:not(.primary) {
  position: relative;
  bottom: 0;
}
.btn-action-group:hover li:not(.primary) .btn {
  -webkit-animation: btn-animation 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-action-group:hover li:not(.primary) .btn i {
  -webkit-animation: icon-animation 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
@-webkit-keyframes btn-animation {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes icon-animation {
  0% {
    transform: scale(0);
  }
  20% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes label-animation {
  0% {
    right: -10px;
    opacity: 0;
  }
  50% {
    right: -10px;
    opacity: 0;
  }
  100% {
    right: 0;
    opacity: 1;
  }
}
